"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { motion } from "framer-motion";
import { Menu, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { useNavigationStore } from "@/lib/store/navigation";

export function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const pathname = usePathname();
  const mainNav = useNavigationStore((state) => state.mainNav);
  const footerContact = useNavigationStore((state) => state.footerContact);

  useEffect(() => {
    setIsOpen(false);
  }, [pathname]);

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <nav className="container flex h-16 items-center">
        <div className="mr-8">
          <Link href="/" className="flex items-center space-x-2">
            <Image
              src="/images/logo.png"
              alt="Automate Your Business"
              width={140}
              height={80}
              className="h-auto w-auto"
            />
          </Link>
        </div>

        {/* Desktop Menu */}
        <div className="hidden md:flex md:flex-1 md:items-center md:justify-between">
          <div className="flex items-center space-x-6">
            {mainNav.map((item, index) => (
              <Link
                key={index}
                href={item.href}
                className={`text-sm font-medium transition-colors hover:text-primary ${
                  pathname === item.href
                    ? "text-foreground"
                    : "text-foreground/60"
                }`}
              >
                {item.title}
              </Link>
            ))}
          </div>
          <div className="flex items-center space-x-4">
            <Button variant="outline" asChild>
              <Link href="/contact">Ücretsiz Danışmanlık</Link>
            </Button>
          </div>
        </div>

        {/* Mobile Menu Button */}
        <div className="flex flex-1 justify-end md:hidden">
          <Button
            variant="ghost"
            className="px-2"
            onClick={() => setIsOpen(!isOpen)}
          >
            <span className="sr-only">Menü</span>
            {isOpen ? (
              <X className="h-6 w-6" />
            ) : (
              <Menu className="h-6 w-6" />
            )}
          </Button>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, x: "100%" }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: "100%" }}
            transition={{ type: "tween" }}
            className="fixed inset-y-0 right-0 w-[300px] bg-background shadow-lg"
          >
            <div className="flex h-16 items-center justify-end px-6">
              <Button
                variant="ghost"
                className="px-2"
                onClick={() => setIsOpen(false)}
              >
                <span className="sr-only">Menüyü Kapat</span>
                <X className="h-6 w-6" />
              </Button>
            </div>
            <div className="px-6 py-6">
              <div className="flex flex-col space-y-4">
                {mainNav.map((item, index) => (
                  <Link
                    key={index}
                    href={item.href}
                    className={`text-base font-medium transition-colors hover:text-primary ${
                      pathname === item.href
                        ? "text-foreground"
                        : "text-foreground/60"
                    }`}
                  >
                    {item.title}
                  </Link>
                ))}
                <Button className="mt-4" asChild>
                  <Link href="/contact">Ücretsiz Danışmanlık</Link>
                </Button>
              </div>
              <div className="mt-8 border-t pt-8">
                <div className="flex flex-col space-y-2">
                  <Image
                    src="/images/logo.png"
                    alt="Automate Your Business"
                    width={140}
                    height={80}
                    className="h-auto w-auto"
                  />
                  <p className="text-sm text-muted-foreground">
                    İşinizi AI ile otomatikleştirin, zamandan ve maliyetten tasarruf edin.
                  </p>
                  <div className="mt-4 space-y-1">
                    <p className="text-sm">
                      <a
                        href={`mailto:${footerContact.email}`}
                        className="text-foreground/80 hover:text-primary"
                      >
                        {footerContact.email}
                      </a>
                    </p>
                    <p className="text-sm">
                      <a
                        href={`tel:${footerContact.phone}`}
                        className="text-foreground/80 hover:text-primary"
                      >
                        {footerContact.phone}
                      </a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </motion.div>
        )}
      </nav>
    </header>
  );
}

